import Vue from 'vue'
import Router from 'vue-router'
import Home from 'components/Home'
import Login from 'components/Login'
import Others from 'components/Others'
import Index from 'components/Index'
import First from 'components/First'
import Second from 'components/Second'
import store from '../store/index'

Vue.use(Router)

// 路由信息对象
// Home 为首页，Login 是登陆页，Others 是其他页
// 其它页分为 Index 导航，First 和 Second 两个子页
// 规定其他页只有登陆后才能进入，此处应用 meta 路由元信息设置是否需要登陆的标志
let routes = [{
  // 路由记录
  path: '/',
  name: 'Home',
  component: Home
}, {
  path: '/home',
  name: 'Home',
  component: Home
}, {
  path: '/login',
  name: 'Login',
  component: Login
}, {
  path: '/others',
  name: 'Others',
  component: Others,
  meta: {
    requiresAuth: true
  },
  children: [{
    // 设置路径为空，父组件会默认加载该组件
    path: '',
    name: 'Index',
    component: Index
  }, {
    path: 'first',
    name: 'First',
    component: First
  }, {
    path: 'second',
    name: 'Second',
    component: Second
  }]
}]

// 路由
const router = new Router({
  routes: routes
})

// 导航钩子
// to/from 都是路由信息对象
// next 是一个函数，用于释放导航钩子
router.beforeEach((to, from, next) => {
  // route.matched 返回一个数组，包含当前路由对象的所有嵌套路径片段的路由记录
  // .some() 方法测试数组中的某些元素是否通过了指定函数的测试
  // 此处查看是否有路由记录的 meta.requiresAuth 返回 true ，返回 true 则进入 if
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // 查看登陆状态
    var isLogin = store.state.isLogin
    console.log('isLogin=' + isLogin)
    if (!isLogin) {
      next({
        path: '/login',
        query: {
          // 使用 query 可在登陆成功后跳转到 to
          // 重定向到 to 的路径
          redirect: to.fullPath
        }
      })
    } else {
      next()
    }
  } else {
    // 必须要有一个 next 释放导航钩子
    next()
  }
})

export default router



