import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLogin: JSON.parse(localStorage.getItem('user')) ? true : false
  },
  mutations: {
    login (states, obj) {
      localStorage.setItem('user', JSON.stringify(obj))
      states.isLogin = true
    },
    exit (states) {
      localStorage.removeItem('user')
      states.isLogin = false
    }
  }
})



